import React,{Component} from "react";
import {Redirect} from 'react-router-dom';
import "./css/Home.css";
import {AuthConsumer} from "../Context";
import imagehome from "../assets/japan-2014618_640.jpg"

export default class Home extends Component{
  render(){
    return(
      <AuthConsumer>
       {(props) => {
          return props.state.isAuthenticate ?
            <div className="Home"> 
            <div className="lander">
              <img src={imagehome} className="home img-fluid rounded" alt="Responsive image"/>
              <h3>A collections of Japan vocabulary cards</h3>
              <p>Token: {props.state.token}</p>
            </div>
            </div>
          : 
            <Redirect to="/login"/>
          }
        }
      </AuthConsumer>

    );
  }
}